<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <div style="margin-top: 15px;">
      <el-input placeholder="请输入页面标题" v-model="title">
        <template slot="prepend">页面标题：</template>
      </el-input>
      <br>
      <br>
      <el-input placeholder="请输入页面路径" v-model="url" class="input-with-select">
        <el-select v-model="select" slot="prepend" placeholder="请选择">
          <el-option label="miaogeng-H5" value="zhaoxitech://com.zhaoxitech.miaogeng/website?title="></el-option>
          <el-option label="cbook-H5" value="zhaoxitech://com.zhaoxitech.cbook/website?title="></el-option>
          <el-option label="huawei-H5" value="zhaoxitech://com.zhaoxitech.cbook.huawei/website?title="></el-option>
          <el-option label="ebook-H5" value="zhaoxitech://com.meizu.media.ebook/website?title="></el-option>
          <el-option label="sanctpalace" value="sanctpalace://com.zhaoxitech.zxbook.common.hybrid.handler.IntentHandler/startActivity?intentUri="></el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-d-arrow-right" @click='handleBtn'></el-button>
      </el-input>
    </div>
    <br>
    <br>
    <el-input v-model="result" readOnly='readOnly'>
      <template slot="prepend">生成的跳转路径：</template>
    </el-input>
  </div>
</template>

<script>
import { Input, Select } from 'element-ui'
const UTILS_JUMP_STORAGE_KEY = 'utils_jump_storage_key'

export default {
  name: 'App',
  components: {
    Input,
    Select
  },
  computed: {
    result () {
      return this.getResult()
    }
  },
  data () {
    var val = localStorage.getItem(UTILS_JUMP_STORAGE_KEY)
    return {
      title: 'test',
      url: '',
      select: val || 'zhaoxitech://com.zhaoxitech.cbook/website?title='
    }
  },
  methods: {
    getResult () {
      return `${this.select}${window.encodeURIComponent(this.title)}&url=${window.encodeURIComponent(this.url)}`
    },
    handleBtn () {
      location.href = this.getResult()
    }
  },
  watch: {
    select (val) {
      localStorage.setItem(UTILS_JUMP_STORAGE_KEY, val)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.el-select .el-input {
  width: 150px;
}
</style>



